* {
  margin: 0;
    padding: 0;
}

.App {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: aliceblue;
    height: 100%;
}

.appWrapper {
    padding-top: 30px;
    padding-bottom: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 80%;
    max-width: 500px;
    background-color: white;
    border-radius: 0 0 5px 5px;
    min-height: 500px;
}

header {
    width: 100%;
    display: flex;
    justify-content: center;
    height: 50px;
    align-items: center;
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    background-color: black;
    color: #fff;
}

.taskNumber {
    margin-top: 30px;
    width: 80%;
    max-width: 500px;
    height: 50px;
    font-family: 'Roboto', sans-serif;
    font-size: 18px;
    background-color: #EC292D;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px 5px 0 0;
}

.taskEnter {
    font-size: 20px;
    font-family: 'Montserrat', sans-serif;
    color: rgb(78, 77, 97);
}

