.task {
    display: grid;
    grid-template-columns: 95fr 5fr;
    align-items: center;
    width: 85%;
    background-color: #fff;
    box-shadow: 2px 2px 5px rgb(238, 238, 238);
    border-radius: 5px;
    padding: 20px;
    color: rgb(55, 51, 51);
    margin-top: 10px;
    margin-bottom: 10px;
    
}

.task:hover {
    transform: scale(1.03);
    transition: 0.2s ease-in-out;
}

.nonCompleted {
    display: flex;
    justify-content: center;
    font-family: "Montserrat", sans-serif;
}

.completed {
    display: flex;
    justify-content: center;
    text-decoration: line-through;
    font-family: "Montserrat", sans-serif;
}

.total {
    background-color: green;
}

.removeButton {
    display: flex;
    align-items: center;
}