.formWrapper {
    width: 80%;
    display: flex;
    margin: 0 auto;
    margin-bottom: 35px;
}

.form {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
}

.input {
    width: 100%;
    height: 30px;
    border: 1px solid rgb(27, 35, 65);
    border-radius: 5px;
    padding-left: 20px;
    margin-bottom: 20px;
}

.buttonStyle {
    width: 50%;
    background-color: #e05a5a;
    border: none;
    color: #fff;
    height: 50px;
    border-radius: 5px;
    cursor: pointer;
}

.buttonStyle:hover {
    background-color: #EC292D;
    transition: 0.2s ease-in-out;
}